














import ut from '../../utils/index.js';
export default {
	data() {
		return {
			userinf:{},
			static:''
		}
	},
	onShow() {
		this.static=ut.static;
		this.userinf=wx.getStorageSync('userinf')||{}
	},
	methods: {
		go_mine_adress(){
			wx.navigateTo({
				url: '../mine/adress'
			})
		},
		uploadimg(){
			ut.uploadimg({
				callback:res=>{
					this.req_uploadimg(res);
					
				}
			})
		},
		req_uploadimg(res){
			console.log(ut.uploadimgurl+"common/imageUpload")
			const that=this;
			wx.uploadFile({
				url: ut.uploadimgurl+"common/imageUpload", //仅为示例，非真实的接口地址
				filePath: res,
				name: 'picture',
				header: {
					"Accept": "*/*"
				},
				success(res) {
					console.log(res)
					if(!res.data){
						ut.totast('上传图片失败')
						return;
					}
					res.data=JSON.parse(res.data);
					if(res.data.code==0){
						that.req_updatePhoto(res.data.data)
					}else{
						ut.totast('上传图片失败')
					}
				}
			})
		},
		req_updatePhoto(photo){
			ut.request({
				data:{
				photo: photo
				},
				url: "user/updatePhone"
			}).then(data => {
				this.userinf.photo=ut.static+photo;
				wx.setStorageSync('userinf',this.userinf);
			})
		},
		go_mine_updatename(){
			wx.navigateTo({
				url: '../mine/updatename'
			})
		}
	}
}
